import React, { useState } from "react";
import Nav from 'react-bootstrap/Nav'
import { Container } from "./styles";

import Main from './Main'
import Fly from './Tecnicas/Fly'
import Monitoramento from './Tecnicas/Monitoramento'
import Shaddowing from './Tecnicas/Shaddowing'
import Heuristica from './Tecnicas/Heuristica'

function Home() {
  const [home, setHome] = useState(true)
  const [flyonthewall, setFlyonthewall] = useState(false)
  const [monitoramento, setMonitoramento] = useState(false)
  const [shadowing, setShadowing] = useState(false)
  const [heuristica, setHeuristica] = useState(false)
  const handleActive = (eventKey) => {
    setHome(false)
    setFlyonthewall(false)
    setMonitoramento(false)
    setShadowing(false)
    setHeuristica(false)

    if(eventKey === 'home'){
      setHome(true)
    } else if(eventKey === 'flyonthewall'){
      setFlyonthewall(true)
    }else if(eventKey === 'monitoramento'){
      setMonitoramento(true)
    }else if(eventKey === 'shadowing'){
      setShadowing(true)
    }else if(eventKey === 'heuristica'){
      setHeuristica(true)
    }else{
      setHome(false)
      setFlyonthewall(false)
      setMonitoramento(false)
      setShadowing(false)
      setHeuristica(false)
  
    }
  }
  return (
    <Container>
      <Nav justify variant="tabs" defaultActiveKey="home">
        <Nav.Item>
          <Nav.Link eventKey="home" onSelect={handleActive}>Home</Nav.Link>
          
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="flyonthewall" onSelect={handleActive}>Mosca na parede</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="monitoramento" onSelect={handleActive}>Monitoramento online</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="shadowing" onSelect={handleActive}>Sombra</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="heuristica" onSelect={handleActive}>Avaliação heurística </Nav.Link>
        </Nav.Item>
        
      </Nav>
     {
       home === true ? (<Main />) : null
     }
     {
       flyonthewall === true ? (<Fly />) : null
       
     }
     {
       monitoramento === true ? (<Monitoramento />) : null
       
     }
     {
       shadowing === true ? (<Shaddowing />) : null
      
     }
     {
        heuristica === true ? (<Heuristica />) : null
     }




    </Container>
  );
}

export default Home;
